
  import { defineComponent } from 'vue';
  import { mapActions, mapGetters } from 'vuex';
  import { PropType } from '@vue/runtime-core';

  interface bookObject {
    author: string,
    authorId: string,
    title: string,
    price: number,
    image: string,
    key: string,
    discount: number | null
  }

  export default defineComponent({
    name: 'BookCard',
    data() {
      return {
        like: false
      }
    },
    props: {
      book: {
        type: Object as PropType<bookObject>,
      },
      imageSize: {
        type: String,
        default: 'M'
      },
    },
    computed: {
      ...mapGetters([
        'favoritesList',
      ]),
      imageSrc(): string{
        return `https://covers.openlibrary.org/b/olid/${this.book?.image}-${this.imageSize}.jpg`
      },
      discountPrice(): number | null{
        return (this.book?.discount) ? Math.ceil(this.book?.price * (100 - this.book?.discount)/100) : null; 
      }
    },
    methods: {
      ...mapActions([
        'toggleFavoritesBook',
        'checkFavoritesBook'
      ]),
      handleLikeButton(bookKey: string){
        this.toggleFavoritesBook(bookKey);
        this.like = !this.like;
        localStorage.setItem('favoritesList', JSON.stringify([...this.favoritesList]));
      }
    },
    mounted(){
      this.checkFavoritesBook(this.book?.key)
        .then(result => {
          this.like = result;
        })
    }
  });
