import { ActionContext } from 'vuex';
import { RootState } from '../index';

interface postsType {
  id: number,
  title: string,
  date: Date
}
export interface postsStateType {
  posts: postsType[],
}

export const blogPosts = ({
  state: () => ({
    posts: [],
  }),
  getters: {
    postsList(state: postsStateType){
      return state.posts;
    },
  },
  mutations: {
    GET_POSTS(state: postsStateType, data: postsType[]) {
      return state.posts = [...data];
    },
  },
  actions: {
    async getPosts({ commit }: ActionContext<postsStateType, RootState>) {
      await fetch(`https://jsonplaceholder.typicode.com/posts/?_limit=29`)
        .then(response => response.json())
        .then(data => {
          const postsList = data.map((post: postsType) => {
            return {
              id: post.id,
              title: post.title,
              date: new Date()
            }
          })
          commit('GET_POSTS', postsList)
        })
    }
  }
});
