
  import { defineComponent } from 'vue';
  import { mapGetters } from 'vuex';
  import { Swiper, SwiperSlide } from 'swiper/vue';
  import BookCard from './BookCard.vue'
  import 'swiper/css';
  import TheLoader from '../components/TheLoader.vue';

  export default defineComponent({
    name: 'BooksSwiper',
    data(){
      return {
        swiper: {} as typeof Swiper,
        loadContent: true
      }
    },
    components: {
      Swiper,
      SwiperSlide,
      BookCard,
      TheLoader
    },
    props: {
      title: {
        type: String
      },
      type: {
        type: String
      }
    },
    computed: {
      ...mapGetters([
        'discountBooksSwiper',
        'popularBooksSwiper',
        'recommendsBooksSwiper'
      ]),
      currentBooksSwiper(){
        return (this.type === 'discount') ? this.discountBooksSwiper : 
        (this.type === 'popular') ? this.popularBooksSwiper : 
        this.recommendsBooksSwiper;
      },
    },
    watch: {
      discountBooksSwiper(){
        this.loadContent = false;
      },
      popularBooksSwiper(){
        this.loadContent = false;
      },
      recommendsBooksSwiper(){
        this.loadContent = false;
      },
    },
    methods: {
      onSwiper(swiper: typeof Swiper){
        this.swiper = swiper;
      },
      changeSlide(direction: string){
        (direction === 'next') ? this.swiper.slideNext() : this.swiper.slidePrev();
      }
    }
  });
